import Categories from "./commons/Categories";
const BottomNav = () => {
  const categories = [
    {
      title: "Free Delivery",
      text: "All orders over $120",
      icon: "fa fa-truck",
      color: "rgb(206, 165, 119)",
    },
    {
      title: "Safe Payment",
      text: "100% secure payment ",
      icon: "fas fa-money-check-alt",
      color: "rgb(86, 224, 217)",
    },
    {
      title: "Shop with confidence",
      text: "If you have probelms",
      icon: "fas fa-key",
      color: "rgb(78, 137, 155)",
    },
    {
      title: "24/7 Help Center",
      text: "Dedicated 24/7 support ",
      icon: "fas fa-user-clock",
      color: "rgb(76, 245, 175)",
    },
    {
      title: "Friendly Services",
      text: "30 day satisfaction guarantee",
      icon: "fa fa-users",
      color: "rgb(224, 86, 109)",
    },
  ];
  return (
    <section id="hero-bottom-nav">
      <Categories categories={categories} />
    </section>
  );
};

export default BottomNav;
