import logo from "../img/logo.svg";
import { NavLink } from "react-router-dom";
import CartDropDown from "./Cart/CartDropDown";

const Navbar = ({ cart, onDelete, onHamburgerClick}) => {
    const totalItemsInCart = cart.reduce((acc, curr) => {
    return acc + curr.quantity
    }, 0)
    const totalAmountInCart = cart.reduce((acc, curr) => {
      return acc + curr.price * curr.quantity
    },0)

  return (
    <nav id="main-nav"
    >
      <div className="container">
        <div className="col-1">
          <img src={logo} alt="Medlilazar" />
        </div>

          <button className='hamburger-icon' onClick={onHamburgerClick}>
            <i class="fas fa-bars"></i>
          </button>
        <div className="col-2">

          <div>
            <input type="text" placeholder="Search..." />
            <div className="search-icon">
              <i className="fas fa-search"></i>
            </div>
          </div>
          <div>
            <h4>sign in/sign up</h4>
            <div className="wish-list-icon">
              <i className="far fa-heart fa-1x"></i>
            </div>
            <div className="cart-icon" >
              <i className="fas fa-cart-plus">
                <span className="count">{totalItemsInCart}</span>
              </i>
              <h4 className="quantity">&nbsp;&nbsp;&nbsp; ${ totalAmountInCart.toFixed(2)}</h4>
              <div className="arrow"></div>
              <CartDropDown cart={cart} onDelete={ onDelete}/>
            </div>
          </div>
          <div>
            <ul>
              <li>
                <NavLink activeClassName="active-nav" exact to="/">
                  Home
                </NavLink>
              </li>
              <li>
                <NavLink activeClassName="active-nav" to="/shop">
                  Shop
                </NavLink>
              </li>
              <li>
                <NavLink activeClassName="active-nav" to="/about">
                  About
                </NavLink>
              </li>
              <li>
                <a href="">Blog</a>
              </li>
              <li>
                <a href="">On sale</a>
              </li>
              <li>
                <a href="">Contact</a>
              </li>
            </ul>
          </div>
        </div>
      </div>
    </nav>
  );
};

export default Navbar;
