import React, { useState } from "react";
import { categories } from "../../products";
import { Link } from "react-router-dom";

const SideShopSearch = ({ isOpen, onToggleSideSearch }) => {
  const [mouseNotOnSS, setMouseNotOnSS] = useState(true);
  return (
    <article className="sd-search">
      {/* Overlay */}
      <div
        className={isOpen ? "overlay" : ""}
        style={{ cursor: mouseNotOnSS ? "pointer" : "" }}
        onClick={mouseNotOnSS ? onToggleSideSearch : null}
      >
        {/* Content*/}
        <div
          className={isOpen ? "content transform" : "content"}
          onMouseOver={() => {
            setMouseNotOnSS(false);
          }}
          onMouseLeave={() => {
            setMouseNotOnSS(true);
          }}
        >
          <span onClick={onToggleSideSearch}>
            CLOSE <i className="fa fa-minus"></i>
          </span>
          <div className="card">
            <h2 className="title">Categories</h2>
            <ul>
              {categories.map((category) => {
                return (
                  <Link to={`/shop/${category}`}>
                    <li>
                      <i className="fas fa-chevron-right"></i> {category}
                    </li>
                  </Link>
                );
              })}
            </ul>
          </div>
        </div>
        {/* Contenr End */}
      </div>
    </article>
  );
};

export default SideShopSearch;
