export const pageSort = (items, action) => {
    var path = action.split('.')[0]
    var order = action.split('.')[1]

    if (typeof items[0][path] === 'number') {
        if (order === 'asc') {
          return  sortPriceAsc(items, path)
        }
        if (order === 'desc') {
          return  sortPriceDesc(items, path)
        }
    } else {
        if (order === 'asc') {
            return sortStringAsc(items, path)
        }
        if (order == 'desc') {
            return sortStringDesc(items, path)
        }
    }

}

function sortPriceDesc(items, path) {
    return items.sort((a, b) => { return b[path] - a[path] })
}

function sortPriceAsc(items, path) {
    return items.sort((a, b) => { return a[path] - b[path] })
}

function sortStringAsc(items, path) {
    return items.sort((a, b) => {
        var x = a[path].toLowerCase().trim();
        var y = b[path].toLowerCase().trim();


        if (x > y) {
            return 1
        }
        if (x < y) {
            return -1
        }
        return 0
    }
    )
}

function sortStringDesc(items, path) {
    return items.sort((a, b) => {
        var x = a[path].toLowerCase().trim();
        var y = b[path].toLowerCase().trim();


        if (x > y) {
            return -1
        }
        if (x < y) {
            return 1
        }
        return 0
    }
    )
}
