import React, { useState } from "react";
import { Link } from "react-router-dom";

const menu = [
  { name: "Home", path: "/" },
  { name: "Shop", path: "/shop" },
  { name: "Blog", path: "/blog" },
  { name: "About", path: "/about" },
  { name: "Contact", path: "/contact" },
];
const SideNav = ({ toggleSideNav, onToggleSideNav }) => {
  const [mouseNotOnSideNav, setMouseNotOnSideNav] = useState(true);

  return (
    <section id="side-nav">
      <div
        onClick={mouseNotOnSideNav ? onToggleSideNav : null}
        className={toggleSideNav ? "overlay" : ""}
        style={{ cursor: mouseNotOnSideNav ? "pointer" : "" }}

      >
        <nav
          className={toggleSideNav ? "content side-nav-transform" : "content"}
          onMouseOver={() => {
            setMouseNotOnSideNav(false);
          }}
          onMouseLeave={() => {
            setMouseNotOnSideNav(true);
          }}
        >
          <button className="p-1" onClick={onToggleSideNav}>
            <i className="fas fa-times"></i>
          </button>
          <ul>
            {menu.map((item) => {
              return (
                <Link to={item.path} onClick={onToggleSideNav}>
                  <li className="p-1">{item.name}</li>
                </Link>
              );
            })}
          </ul>
        </nav>
      </div>
    </section>
  );
};

export default SideNav;
