const SortCount = ({
  productCategory,
  currentPage,
  totalProducts,
  pageSize,
  products,
}) => {
  const startIndex = (currentPage - 1) * pageSize + 1;
  let endIndex = currentPage * pageSize;
  if (endIndex > totalProducts) {
    endIndex = totalProducts;
  }

  return (
    <div>
      {!productCategory && (
        <h3>
          Showing {startIndex} - {endIndex} of {totalProducts} results
        </h3>
      )}
      {productCategory && renderSortResult(products)}
    </div>
  );
};

export default SortCount;
function renderSortResult(products) {
  const results =
    products.length > 1
      ? `Showing ${products.length} results`
      : `Showing the single result`;
  return <h3>{results}</h3>;
}
