import { React, Component } from "react";
import { getCart } from "../../cart";

class CartDropDown extends Component {
  state = {
    cart: "",
  };

  render() {
    const { cart, onDelete } = this.props;
    const totalAmountInCart = cart.reduce((acc, curr) => {
      return acc + curr.price * curr.quantity;
    }, 0);

    if (cart.length === 0) {
      return (
        <div className="cart-dd empty ">
          <i class="fas fa-times fa-5x"></i>
          <p>Cart is empty</p>
        </div>
      );
    }
    return (
      <article className="cart-dd">
        <div className="head">
          <span>Subtotal:</span> <h2> $ {totalAmountInCart.toFixed(2)}</h2>
        </div>
        {cart.map((item) => {
          return (
            <div className="card">
              <img src={item.image} alt={item.name} />

              <h5>{item.name}</h5>

              <div className="price-x-quantity">
                <span>{item.quantity}</span> X{" "}
                <span className="price">${item.price.toFixed(2)}</span>
              </div>

              <button onClick={ ()=>onDelete(item)}>X</button>
            </div>
          );
        })}
        <footer>
          <button className='btn-1'>View Cart</button> <button className='btn-2'>Check out</button>
        </footer>
      </article>
    );
  }
}

export default CartDropDown;
