import React, { Component } from 'react';
import fromOurBlog from '../../fromOurBlog';
import { paginate } from './../../Utilities/paginate';
import CartFooter from './commons/CardFooter';
import GridComponent from './commons/GridComponent';

class FromOurBlog extends GridComponent {
    state = {
        data: fromOurBlog,
        startIndex: 0,
        pageSize:4
    }
    render() {
        const { data, startIndex, pageSize } = this.state;

        const paginatedData = paginate(data, startIndex, this.getPageSize(pageSize, 2, 1, this.props.homeWidth))

        return (<section className="wrapper grid-products bg-white br-4" id="from-our-blog">
            {this.renderHeader('From Our blog', startIndex, this.getPageSize(pageSize, 2, 1, this.props.homeWidth), data)}
            <div className="content">
                {paginatedData.map(item => {
                   

                    return <article className="card">
                   
                        <img src={item.image} alt={item.title}/>
                        <small>POSTED ON: <span>14-OCT-2020</span></small>
                        <h3>{item.title}</h3>
                        <div className="py1">
                            <CartFooter
                            text='READ MORE'/>
                        </div>
                </article>
            })}
            </div>
            
        </section> );
    }
}
 
export default FromOurBlog;