import React, { Component } from "react";
import GridComponent from "./commons/GridComponent";
import { paginate } from "./../../Utilities/paginate";
import featuredBrands from '../../featuredBrands'
class FeaturedBrand extends GridComponent {
  state = {
    products: featuredBrands,
    startIndex: 0,
    pageSize: 4,
  };
  render() {
    const { startIndex, pageSize, products } = this.state;
    const tablet = this.props.homeWidth < 1200

    const paginatedProducts = paginate(products, startIndex, this.getPageSize(pageSize, 4, 1, this.props.homeWidth));
    return (
      <section id="featured-brands" className="grid-products ">
        {this.renderHeader("Featured Brands", startIndex, pageSize, products)}
        <div className="grid">
          {paginatedProducts.map((product) => {
            const brandStyle = {
              background: `${product.brand} no-repeat center center/100% 100%`
            };

            const companyStyle = {
              background: `#fff ${product.company} no-repeat center center/50%`,
             
            };
            return (
              <article className="card">
                <div className="brands" style={brandStyle}></div>
                <div className="company" style={companyStyle}></div>
              </article>
            );
          })}
        </div>
      </section>
    );
  }
}

export default FeaturedBrand;
