import { useState, useEffect } from "react";
import DailyDeal from "../Home/DailyDeal";
import FeaturedBrand from "../Home/FeaturedBrands";
import HealthProducts from "../Home/HealthProducts";
import Hero from "../Home/Hero";
import PopularCategories from "../Home/PopularCategories";
import TrendingProducts from "../Home/TrendingProducts";
import FromOurBlog from "./../Home/FromOurBlog";

const Home = ({ cart, addToCart }) => {
  const homeWidth = useWidth();

  return (
    <section id='home' className="container">
      <Hero />
      <div style={{ display: "flex", flexDirection:'column', gap:"3rem"}}>
        <DailyDeal addToCart={addToCart} homeWidth={homeWidth} />
        <FeaturedBrand  homeWidth={homeWidth} />
        <HealthProducts homeWidth={homeWidth} />
        <PopularCategories homeWidth={homeWidth} />
        <TrendingProducts homeWidth={homeWidth} />
        <FromOurBlog homeWidth={homeWidth}/>
      </div>
    </section>
  );

  function useWidth() {

    const [homeWidth, sethomeWidth] = useState(undefined);

    useEffect(() => {
      function getWidth() {
        sethomeWidth(window.innerWidth);
      }
      window.addEventListener("resize", getWidth);
      getWidth();
      return () => window.removeEventListener("resize", getWidth);
    }, []);
    return homeWidth;
  }
};

export default Home;
