import React, { useState, useEffect } from 'react';
import './App.css';
import './tablet.css'
import { Route, Switch } from 'react-router-dom'
import Navbar from './components/Navbar';
import Header from './components/Header';
import Home from './components/pages/Home';
import About from './components/pages/About';
import Shop from './components/pages/Shop';
import NotFound from './components/pages/Not-Found';
import { addToCart } from './cart';
import SideNav from './components/Navbar/SideNav';
import BottomNav from './components/Navbar/BottomNav';

function App() {
  const [toggleSideNav, setToggleSideNav] = useState(false)

  const [cart, setCart] = useState(
    localStorage.getItem('cart')
      ? JSON.parse(localStorage.getItem('cart'))
      : []
  )

  useEffect(() => {
    localStorage.setItem('cart', JSON.stringify(cart))

    toggleSideNav
      ? document.body.style.overflowY = 'hidden'
      : document.body.style.overflowY = 'unset'

  })

  return (
    <React.Fragment>

      <SideNav onToggleSideNav={handleToggleSideNav} toggleSideNav={toggleSideNav} />

      <main className={toggleSideNav ? 'main-app transform-main-app' : 'main-app'}>

        <Header />
        <Navbar cart={cart} onDelete={handleDelete} onHamburgerClick={handleToggleSideNav} />

        <Switch>

          <Route exact path='/' render={(props) => <Home {...props} addToCart={addToCart} />} />
          <Route exact path='/shop' render={(props) => <Shop {...props} addToCart={addToCart} />} />
          <Route path='/shop/:id' render={(props) => <Shop {...props} addToCart={addToCart} />} />
          <Route exact path='/about' component={About} />
          <Route path='/about/product-category/:id' component={About} />
          <Route path='*' component={NotFound} />

        </Switch>
      </main>
      <BottomNav cart={ cart}/>

    </React.Fragment>
  );
  function handleDelete(product) {
    let updatedCart = cart.filter(item => item.id !== product.id)
    setCart(updatedCart)

  }

  function addToCart(product) {
    const newItem = cart.find(i => i.name === product.name)
    let updatedCart;

    if (!newItem) {
      updatedCart = [...cart, product]
    }

    else {
      updatedCart = cart.map(item => {
        return (
          item.name === product.name
            ? { ...item, quantity: item.quantity + 1 }
            : { ...item }
        )
      })
    }
    setCart(updatedCart)
  }

  function handleToggleSideNav() {
    setToggleSideNav(!toggleSideNav)
  }

}

export default App;
