import { Link } from "react-router-dom";
import CardFooter from "./../Home/commons/CardFooter";
const ProductsListView = ({ products }) => {
  return (
    <section
      id="products-list-view"
     
    >
      <div className="content">
        <p>List View</p>
        {products.map((product) => {
          return (
            <article
              className="card"
              
            >
              <img src={product.image} alt="" />

              <div className="col-2">
                <div>
                  {product.category.map((p) => {
                    if (p !== product.category[product.category.length - 1]) {
                      return (
                        <Link to={`/shop/${p}`}>
                          {" "}
                          <small>{p}, </small>
                        </Link>
                      );
                    } else {
                      return (
                        <Link to={`/shop/${p}`}>
                          {" "}
                          <small>{p} </small>
                        </Link>
                      );
                    }
                  })}

                  <h3>{product.name}</h3>
                </div>

                <h3 className={product.formerPrice ? "new-price" : ""}>
                  {product.price} <strike>{product.formerPrice}</strike>
                </h3>
                <p>
                  Lorem ipsum dolor sit amet, consectetur adipisicing elit, sed
                  do eiusmod tempor incididunt ut labore et dolore magna aliqua.
                  Ut enim ad minim veniam, quis nostrud exercitation ullamco
                  laboris nisi ut aliquip.
                </p>
                <CardFooter text="Add to cart" />
              </div>
            </article>
          );
        })}
      </div>
    </section>
  );
};

export default ProductsListView;
