import React, { useState } from "react";
import { categories } from "../../products";
import { Link } from "react-router-dom";
const ProductSearch = ({productCategory}) => {
  const [active, setActive] = useState("");
  return (
    <section
      className="shop-search br-4"
    >
      <article>
        <h2 className="title">Category</h2>
        <div className="content">
          {categories.map((name) => {
            return (
              <Link
                className="link"
                to={`/shop/${name}`}
                key={name}
                onClick={() => setActive(name)}
              >
                <i class="fas fa-chevron-right"></i> <span className={productCategory === name ? "active-transform" : ""}>
                  {name}
                </span>{" "}
              </Link>
            );
          })}
        </div>
      </article>
    </section>
  );
};

export default ProductSearch;
