import Categories from "./commons/Categories";

const TopNav = () => {
  const categories = [
    {
      title: "Medicine",
      text: "Over 25000 products ",
      icon: "fas fa-prescription-bottle-alt fa-fill",
      color: "rgb(38, 118, 124)",
    },
    {
      title: "Wellness",
      text: "Health products ",
      icon: "fas fa-spa",
      color: "rgb(224, 86, 109)",
    },
    {
      title: "Diagnostic",
      text: "Book tests & checkups ",
      icon: "fa fa-stethoscope",
      color: "rgb(78, 155, 78)",
    },
    {
      title: "Health Corner",
      text: "Trending from health experts ",
      icon: "fa fa-heartbeat",
      color: "rgb(78, 114, 155)",
    },
    {
      title: "Others",
      text: "More info ",
      icon: "fa fa-clipboard",
      color: "rgb(245, 186, 76)",
    },
  ];
  return <Categories categories={categories} />;
};

export default TopNav;
