import React from "react";
import CategoryCard from "./CategoryCard";

const Categories = ({categories}) => {
  
  return (
    <React.Fragment>
      <section id="categories">
          {categories.map((category) => {
            return (
              
                <CategoryCard
                    key={category.title }
                    category={category} />
            );
          })}
      </section>
    </React.Fragment>
  );
};

export default Categories;
