import React, { Component } from "react";

const sortList = [

  { path: "name.asc", name: "Default Sorting" },
  { path: "name.asc", name: "Sort by name: A-Z" },
  { path: "name.desc", name: "Sort by name: Z-A" },
  { path: "price.asc", name: "Sort by price: low to high " },
  { path: "price.desc", name: "Sort by price: high to low " },
];

class SortOptions extends Component {
  state = {
    account: { username: "" },
  };

  render() {
    return (
      <React.Fragment>
        <select
          name="sortOptions"
          id="sort-options"
          onChange={this.props.onOptionChange}
        >
          {sortList.map((item) => {
            return (
              <option value={item.path} key={item.name}>
                {item.name}
              </option>
            );
          })}
        </select>
      </React.Fragment>
    );
  }
}

export default SortOptions;
