import React from "react";

const Paginate = ({ pageSize, totalProducts, currentPage, onButtonClick }) => {
  const numberOfPages = Math.ceil(totalProducts / pageSize);
  if (numberOfPages === 1) return null;
  const showPrev = currentPage > 1;
  const showNext = currentPage * pageSize <= totalProducts;
  const paginate = new Array(numberOfPages).fill(null).map((i, ind) => {
    return ind + 1;
  });

  return (
    <div className="paginate">
      {showPrev && (
        <button onClick={() => onButtonClick("prev")}>&lt; &nbsp;PREV </button>
      )}
      {paginate.map((item) => {
        return (
          <button
            key={item}
            onClick={() => onButtonClick(item)}
            className={
              item === currentPage ? "round-icon active" : "round-icon"
            }
          >
            {item}
          </button>
        );
      })}
      {showNext && (
        <button onClick={() => onButtonClick("next")}>NEXT &nbsp; &gt;</button>
      )}
    </div>
  );
};

export default Paginate;
