import CardFooter from "../commons/CardFooter";
import { Link, NavLink } from "react-router-dom";

const ProductCard = ({ product, addToCart }) => {
  return (
    <article className="card">
      <img src={product.image} alt="" />
      <div>
        {product.category.map((p, i, arr) => {
          const last = p === arr[arr.length - 1];

          return !last ? (
            <Link to={`/shop/${p}`}>
              <small>{p}, </small>
            </Link>
          ) : (
            <Link to={`/shop/${p}`}>
              <small>{p} </small>
            </Link>
          );
        })}

        <h3>{product.name}</h3>
      </div>

      <h3 className={product.formerPrice ? "new-price" : ""}>
        ${product.price} <strike>{product.formerPrice}</strike>
      </h3>
      <div
        onClick={() => {
          addToCart(product);
        }}
      >
        <CardFooter text="Add to cart" />
      </div>
    </article>
  );
};

export default ProductCard;
