import ProductCard from "./ProductCard";


const Products = ({ products, gridCount, addToCart }) => {
    return (
      <div
        className="content"
        style={{ gridTemplateColumns: `repeat(${gridCount}, 1fr)` }}
      >
        {products.map((product) => {
          return (
            <ProductCard
              product={product}
              addToCart={addToCart}
            />
          );
        })}
            </div>
     );
}
 
export default Products;