import { storeProducts } from "../../products";
import { Route, Switch, Link } from "react-router-dom";
import Paginate from "../commons/Paginate";
import Products from "./../Home/ProductGrid/Products";
import SortOptions from "./../commons/SortOptions";
import SortCount from "./SortCount";
import ProductsListView from "./ProductsListVew";
import { useState } from "react";
import SideShopSearch from "./SideShopSearch";

const ShopProducts = ({
  products,
  pageSize,
  currentPage,
  totalProducts,
  onButtonClick,
  onOptionChange,
  productCategory,
  getCategory,
  addToCart,
}) => {
  const [isOpen, setIsOpen] = useState(false);
  
  function handleToggleSideSearch() {
    setIsOpen(!isOpen);
 }
 console.log(isOpen)

  if (productCategory && getCategory.length < 1) {
    return <h2>No Products Found</h2>;
  }

  return (
    <section className="shop-list ">
      <SideShopSearch
        isOpen={isOpen}
        onToggleSideSearch={handleToggleSideSearch}
      />
      <article>
        <div className="sorting-wrapper py-1">
          <div>
            <button  onClick={handleToggleSideSearch}>Filter</button>

            <SortCount
              productCategory={productCategory}
              currentPage={currentPage}
              pageSize={pageSize}
              products={products}
              totalProducts={totalProducts}
            />
          </div>
          <div>
            <button>
              <Link to="/">Grid</Link>
            </button>
            <button>
              <Link to="/">List</Link>
            </button>
            <SortOptions onOptionChange={onOptionChange} />
          </div>
        </div>
        <hr />

        <div className="grid-products">
          <Products products={products} gridCount="3" addToCart={addToCart} />
        </div>

        <Paginate
          pageSize={pageSize}
          products={products}
          totalProducts={totalProducts}
          currentPage={currentPage}
          onButtonClick={onButtonClick}
        />
      </article>
    </section>
  );

};

export default ShopProducts;
