import React, { Component } from "react";
import products from "../../../products";
import Products from "./Products";
import { paginate } from "../../../Utilities/paginate";
import ProductsHeader from "./ProductsHeader";
import GridComponent from "../commons/GridComponent";

class DailyDeal extends GridComponent {
  state = {
    products: this.props.products,
    startIndex: this.props.startIndex,
    pageSize: this.props.pageSize,
  };

  render() {
    const { products, startIndex, pageSize } = this.state;
    const { addToCart, homeWidth } = this.props;

    const paginatedProducts = paginate(
      products,
      startIndex,
      this.getPageSize(pageSize, 3,1, homeWidth)
    );
    return (
      <div className="wrapper ">
        <div className="grid-products">

        {this.renderHeader(this.props.title, startIndex, pageSize, products)}

        <Products
          products={paginatedProducts}
          gridCount={pageSize}
          addToCart={addToCart}
        />
        </div>
      </div>
    );
  }
}

export default DailyDeal;
