import React from "react";
import { Route, Link } from "react-router-dom";
import ProductsListView from "../Shop/ProductsListVew";

const About = () => {
  return (
    
    <section id="about">
      <h2>About</h2>
      <Link to="/about/listview">List View</Link>
      <Route path="/about/listview" component={ProductsListView} />
    </section>
  );
};

export default About;
