import popularCategories from "../../popularCategories";
import GridComponent from "./commons/GridComponent";
import { paginate } from "../../Utilities/paginate";

class PopularCategories extends GridComponent {
  state = {
    startIndex: 0,
    endIndex: 7,
    data: popularCategories,
  };
  render() {
    const { startIndex, endIndex, data } = this.state;
    const paginatedData = paginate(data, startIndex,  this.getPageSize(endIndex, 3,2, this.props.homeWidth));
    return (
      <section id="popular-categories" className="grid-products wrapper br-4">
        {this.renderHeader("Popular Categories", startIndex, endIndex, data)}
       {data.length}
        <div className="grid">
          {paginatedData.map((item) => {
            return ( 
              <article className="card">
                    <img src={item.image} alt={item.category} className="br-4" />
                <p>{item.category}</p>
              </article>
            );
          })}
        </div>
      </section>
    );
  }
}

export default PopularCategories;
