import React from "react";
import { storeProducts } from "../../products";
import RouteComponent from "./../commons/RouteComponent";
import ShopList from "../Shop/ShopList";
import ShopSearch from "../Shop/ShopSearch";
import { pagePaginate } from "./../../Utilities/pagePaginate";
import { pageSort } from "./../../Utilities/pageSort";

class Shop extends RouteComponent {
  state = {
    products: storeProducts,
    currentPage: 1,
    pageSize: 14,
    sortOptions: "name.asc",
  };

  getCategory(data) {
    return data.filter((i) => {
      return i.category.includes(this.props.match.params.id);
    });
  }

  handlePageChange = (pageNumber) => {
    let currentPage = this.state.currentPage;
    if (pageNumber === "prev") {
      currentPage--;
    } else if (pageNumber === "next") {
      currentPage++;
    } else {
      currentPage = pageNumber;
    }

    this.setState({ currentPage });
    console.log(pageNumber);
  };

  handleSortChange = (e) => {
    this.setState({ sortOptions: e.currentTarget.value });
  };

  render() {
    const { id: productCategory } = this.props.match.params;
    const { pageSize, currentPage, sortOptions } = this.state;
    const getCategory = this.getCategory(storeProducts);

    const filteredProducts =
      getCategory.length > 0? getCategory : storeProducts;
    const sorted = pageSort(filteredProducts, sortOptions);
    const paginatedProducts = pagePaginate(sorted, currentPage, pageSize);
    return (
      <section id="shop"
      style={{ position: 'sticky', top:0}}
      >
        <div className="body-container">
          {this.renderSectionNav(productCategory, "Products", "/shop")}
          <div className="content-grid">
            <ShopList
              products={paginatedProducts}
              pageSize={pageSize}
              currentPage={currentPage}
              totalProducts={filteredProducts.length}
              productCategory={productCategory}
              getCategory={getCategory}
              onButtonClick={this.handlePageChange}
              onOptionChange={this.handleSortChange}
              addToCart={this.props.addToCart}
            />

            <ShopSearch productCategory={productCategory} />
          </div>
        </div>
      </section>
    );
  }
}

export default Shop;
