import React, { Component } from "react";
import ProductsHeader from "../ProductGrid/ProductsHeader";

class GridComponent extends Component {
  handleButtonClick = (action) => {
    let startIndex = this.state.startIndex;
    action === "next" ? startIndex++ : startIndex--;
    this.setState({ startIndex });
    console.log(startIndex);
  };

  renderHeader = (title, startIndex, pageSize, products) => {
    return (
      <ProductsHeader
        title={title}
        handleButtonClick={this.handleButtonClick}
        startIndex={startIndex}
        pageSize={pageSize}
        products={products}
      />
    );
  };

  getPageSize(pageSize, tabletPageSize, mobilePageSize, homeWidth) {
    let endIndex = pageSize;
    if (homeWidth > 760 && homeWidth <= 1200)
      endIndex = tabletPageSize;
    else if (homeWidth <= 1200) {
      endIndex = mobilePageSize;
    }
    return endIndex;
  }
}

export default GridComponent;
