import React, { Component } from "react";
import SectionNav from "./sectionNav";

class RouteComponent extends Component {
  renderSectionNav = (params, sectionTitle, path) => {
    return (
      <div className="section-nav">
        <SectionNav section="Medilizar" path="/" />
        {params && <SectionNav section={sectionTitle} path={path} />}
        {params && <h3>{params}</h3>}
        {!params && <h3>{sectionTitle}</h3>}
      </div>
    );
  };

  renderSortOptions = () => {
    const sortList = [
      { path: "title", name: "Default Sorting", action: "asc" },
      { path: "price", name: "Sort by price: low to high ", action: "desc" },
      { path: "price", name: "Sort by price: high to low ", action: "asc" },
    ];

    function handleChange(e) {
      console.log(e.target);
    }

    function handleSortChange(path, action) {
      console.log(path, action);
    }
    return (
      <select name="sort-options" id="sort-options" onChange={handleChange}>
        {sortList.map((item) => {
          return (
            <option
              action={item.action}
              value={item.path}
              key={item.name}
              onClick={() => handleSortChange(item.path, item.action)}
            >
              {item.name}{" "}
            </option>
          );
        })}
      </select>
    );
  };
}

export default RouteComponent;
